import React from 'react';
import Header from './Header';
import styled from 'styled-components';

const StyledPage = styled.div`
    background: white;
    color: black;
`;

const Inner = styled.div`
    max-width: 1000px;
    margin: 0 auto;
    padding: 2em;
`;

class Page extends React.Component {
    render() {
        return(
            <StyledPage>
                <Inner>
                    <Header/>
                    {this.props.children}
                </Inner>
            </StyledPage>
        );
    }
}

export default Page;